@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
nav {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 5rem;
  width: 100vw;
  padding: 0.2em 1em;
  background: #dff1fd;
  box-shadow: 0px 0.25rem 1.25rem rgba(0, 0, 0, 0.25);
}
.nav-item {
  margin: 0 4.5em;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}
.nav-item:hover {
  filter: brightness(155%);
}
.logo {
  width: 4.188rem;
  height: 4.188rem;
}
.nav-icon {
  height: 1.875rem;
  width: 1.875rem;
}
.nav-item-links img {
  display: none;
}
.nav-link {
  text-transform: capitalize;
  font-weight: 300;
  font-size: 1.25rem;
}
.nav-search-from {
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-input {
  width: 21.25rem;
  height: 1.875rem;
  border-radius: 1.25rem 0 0 1.25rem;
}
.nav-search-from input[type="text"] {
  background: #fff;
  border: none;
  text-align: center;
}
.nav-search-from input[type="text"]:focus-visible {
  border: none;
}

.search-submit {
  height: 1.25rem;
  width: 1.25rem;
}

.search-btn {
  height: 1.875rem;
  width: 2.5rem;
  border: none;
  background: #fff;
  transition: all 0.5s ease-in-out;
  border-radius: 0 1.25rem 1.25rem 0;
  cursor: pointer;
}
.search-btn:hover {
  filter: brightness(50%);
}

footer {
  position: relative;
  left: 0;
  bottom: 0;
  width: 100vw;
  height: 3.125rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.2em 1em;
  background: #dff1fd;
}
.foot-item {
  font-weight: 300;
  font-size: 1.25rem;
  letter-spacing: 0.2em;
}
.foot-center {
  font-weight: normal;
  font-size: 1.563rem;
}
.foot-left,
.foot-right {
  position: relative;
  cursor: pointer;
}

.foot-left {
  right: 25em;
  text-transform: capitalize;
}
.foot-right {
  left: 23em;
  display: flex;
  flex-direction: column;
  text-decoration: underline;
}

/* home containers css */
.home-con {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 4rem;
  gap: 4rem;
  margin-bottom: 2em;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  justify-content: center;
  align-items: center;
}
/* home top css */
.home-top-card {
  width: 41.875rem;
  height: 17.75rem;
  background: #d93d3e;
  box-shadow: 0px 0.25rem 0.25rem rgba(0, 0, 0, 0.25);
  border-radius: 1.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.home-top-card-img {
  width: 24.688rem;
  height: 17.75rem;
  border-radius: 1.25rem 0 0 1.25rem;
}
.home-top-card-text {
  width: 17.188rem;
  height: 17.75rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
}
.home-top-card-title {
  width: 14.563rem;
  height: 4.063rem;
  font-weight: normal;
  font-size: 1.875rem;
  text-align: center;
  text-transform: capitalize;
  color: #fff;
}
.home-top-card-detail {
  font-weight: 300;
  font-size: 1.25rem;
  color: #fff;
}
.home-top-card-btn {
  width: 7.5rem;
  height: 2.688rem;
  background: #9c0001;
  color: #fff;
  border: none;
  font-weight: 500;
  font-size: 1.25rem;
  border-radius: 0.625rem;
  transition: all 0.5s ease-in-out;
  cursor: pointer;
}
.home-top-card-btn:hover {
  filter: brightness(90%);
}
/* home-mid css */
.home-mid {
  grid-template-columns: repeat(5, 1fr);
}
.home-mid-card {
  width: 5rem;
  height: 5rem;
  background: #e0f0fa;
  box-shadow: 0px 0.25rem 0.25rem rgba(0, 0, 0, 0.25);
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 1.563rem;
  text-transform: capitalize;
  text-align: center;
  padding: 0.2em;
  transition: all 0.5s ease-in-out;
  border-radius: 1.25rem;
}
.home-mid-card:hover {
  transform: translateY(-0.5em);
}
/* home btm css*/
.home-btm-card {
  width: 25rem;
  height: 12.5rem;
  box-shadow: 0px 0.25rem 0.25rem rgba(0, 0, 0, 0.25);
  border-radius: 1.25rem;
  transition: all 0.5s ease-in-out;
}
.home-btm-card:hover {
  transform: translateY(0.5em);
}
.home-btm-img {
  width: 25rem;
  height: 12.5rem;
  border-radius: 1.25rem;
}

.home-btm-card-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  bottom: 12.5rem;
  color: #fff;
  font-weight: 300;
  font-size: 2.5rem;
  text-transform: capitalize;
  width: 25rem;
  height: 12.5rem;
  transition: all 0.5s ease-in-out;
  border-radius: 1.25rem;
}
.home-btm-card-text:hover {
  -webkit-backdrop-filter: blur(0.25rem);
          backdrop-filter: blur(0.25rem);
}
.home-btm-card-title {
  font-weight: normal;
  font-size: 3.125rem;
}

.cards-title {
  font-weight: normal;
  font-size: 50px;
  width: 90vw;
  text-align: left;
}
.cards-con {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2rem;
  gap: 2rem;
}

.card {
  width: 17.875rem;
  height: 24.563rem;
  background: #eff9ff;
  box-shadow: 0.063rem 0.188rem 0.313rem rgba(0, 0, 0, 0.25);
  border-radius: 1.25rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  transition: all 0.5s ease-in-out;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.card:hover {
  transform: translateY(-1em);
}
.card-img {
  height: 11.563rem;
  width: 17.813rem;
  border-radius: 1.25rem 1.25rem 0 0;
}
.card-text {
  width: 15.625rem;
  height: 3.125rem;
  font-weight: normal;
  font-size: 1.875rem;
  text-align: center;
}
.card-title {
  margin: 0.5em 1em;
}
.card-price {
  color: #438fc4;
}
.card-discount {
  color: #57a059;
}
.card-price,
.card-discount {
  text-align: left;
}

@media only screen and (max-width: 565px) {
  /* nav bar css*/
  nav {
    display: grid;
    grid-template-columns: repeat(5, 2fr);
    height: auto;
    max-width: 95vw;
    justify-items: center;
    align-items: center;
    padding: 0.5em;
    grid-gap: 0.5rem;
    gap: 0.5rem;
  }
  .nav-item {
    margin: 0;
    width: 1.875rem;
  }
  .logo {
    width: 4.188rem;
  }
  .nav-item-links img {
    display: flex;
  }
  .nav-item-links .nav-link {
    display: none;
  }
  /* search bar css */
  .search-bar {
    order: 6;
    width: 4rem;
  }
  .search-btn {
    height: 2rem;
  }
  .container {
    width: 100vw;
    padding: 2em 0 !important;
  }
  /* home genreal css*/
  .home-con {
    grid-template-columns: repeat(1, 0fr);
    justify-items: center;
  }
  /* home top css*/
  .home-top-card {
    width: 21rem;
    height: 16rem;
  }

  .home-top-card-img {
    height: 16rem;
    width: 10rem;
  }
  .home-top-card-text {
    height: 16rem;
    width: 11rem;
  }
  .home-top-card-title {
    width: 11rem;
    font-size: 1.8rem;
  }
  /* home mid css*/
  .home-mid {
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
  }
  .home-mid-card {
    height: 4rem;
    width: 4rem;
    font-size: 1rem;
  }
  /* home btm css */
  .home-btm-card,
  .home-btm-img,
  .home-btm-card-text {
    width: 22rem;
  }
  /* cards css*/
  .cards-con {
    grid-template-columns: repeat(2, 1fr);
  }
  /* card css */
  .card {
    width: 11rem;
    height: 17.8rem;
  }
  .card img {
    width: 11rem;
    height: 7rem;
  }
  .card-text {
    width: 10rem;
  }
  .card-text {
    padding: 0 0.5rem;
  }
  footer {
    height: 10rem;
    flex-direction: column;
    padding: 0;
    grid-gap: 1em;
    gap: 1em;
  }
  .foot-left,
  .foot-right {
    position: relative;
    top: 0;
    right: 0;
    left: 0;
    text-align: center;
  }
}

* {
  font-family: "Roboto", sans-serif;
}
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

.container {
  padding: 2rem 1em;
  display: flex;
  height: auto;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

